// mixin's
@mixin vc-height($height) {
  height: $height;
  line-height: $height; }
@mixin anim() {
  transition: all 0.3s ease; }
@mixin selectnone() {
  user-select: none;
  cursor: pointer; }
@mixin border() {
  border: 1px solid black; }
@mixin popup() {
  position: absolute;
  top: 45px;
  right: 120px;
  bottom: 0;
  height: 300px;
  width: 550px;
  background-color: rgba(white, 0.95); }



// animation

.loading {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-color: rgba(white, 0.30);
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 5;
  top: 0px;
  left: 0px;
  div {
    width: 55px;
    height: 55px;
    margin: 100px auto;
    background-color: $clrprim2;
    border-radius: 100%;
    animation: sk-scaleout 1.0s infinite ease-in-out; } }


@keyframes sk-scaleout {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1.0);
    opacity: 0; } }


