@mixin bgs() {
  background: {
    repeat: no-repeat;
    size: cover;
    position: center; }
  color: white; }
section.service {
  width: 100%;
  // margin-bottom: 150px
  // transition: all 1s ease-in-out
  // &.animate
  //     margin-bottom: 0px
  div {
    height: 75vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    font-family: roboto-light;
    h2 {
      font-family: h-u-light;
      text-transform: uppercase;
      font-size: 2.5em;
      letter-spacing: 2px; }
    p {
      text-transform: capitalize;
      letter-spacing: 1px; } } }
.s-seo {
  background-image: url('../img/service-a.png');
  @include bgs(); }
